import React, {Component} from 'react';
import shortnerStyles from "../components/shortener.module.css";
import isValidURL from "../helpers/validators";
import Information from "../components/information";

class Shortener extends Component {
    constructor(props) {
        super(props);
        this.state = {value: 'goog.co', submited: false};

        this.handleSubmit = this.handleSubmit.bind(this);
        this.logDat = this.logDat.bind(this);
    }

    handleChange(event) {
        this.setState({value: event.target.value});
    }

    logDat(response) {
        console.log(response.json())
    }

    handleSubmit(event) {
        const {value} = this.state;
        const isValidSubmit = value && isValidURL(value);
        event.preventDefault();
            /*.then(function(res) {
        return ({
            value: value,
            description: '',
            extraVars: {
                hwid: res,
                generateInviteCode: true
            }
        })}
    ).then((res) => fetch('https://jsonplaceholder.typicode.com/todos/1', {method: "POST",
            body: JSON.stringify(res)})
                .then(response => this.setState({submited: isValidSubmit, value: value}))
            );*/
    }

    render() {
        const {submited, value} = this.state;
        return (
            <div className={shortnerStyles.common}>
                <h3 className={shortnerStyles.heading}>
                    The link knows all, so can you.
                </h3>
                <form onSubmit={this.handleSubmit}>
                    <input className={shortnerStyles.input} type="text" value={value}
                           onChange={this.handleChange}/>
                    <div className={shortnerStyles.buttonGroup}>
                        <input className={shortnerStyles.cta} type="submit" value="Short this shit!"/>
                    </div>
                </form>
                {submited && <Information urlValue={value} shortedUrl="http://krtsh.co"/>}
            </div>
        );
    }
}

export default Shortener;