import React, {Component} from 'react';
import styles from "../components/information.module.css";

class Information extends Component {
    state = {
        isSubscribed: false
    };

    copyToClipboard = (str) => {
        const el = document.createElement('textarea');
        el.value = str;
        el.setAttribute('readonly', '');
        el.style.position = 'absolute';
        el.style.left = '-9999px';
        document.body.appendChild(el);
        const selected =
            document.getSelection().rangeCount > 0
                ? document.getSelection().getRangeAt(0)
                : false;
        el.select();
        document.execCommand('copy');
        document.body.removeChild(el);
        if (selected) {
            document.getSelection().removeAllRanges();
            document.getSelection().addRange(selected);
        }
    };
    componentWillReceiveProps() {
        window.Pushwoosh.isSubscribed()
            .then((res) => this.setState({isSubscribed: res}));
    }

    subscribe = () => window.Pushwoosh.subscribe();

    render() {
        const {urlValue, shortedUrl} = this.props;
        return (
            <div className={styles.common}>
                <div className={styles.urlValue}>
                    <span>{urlValue}</span>
                </div>
                <div className={styles.shorted}>
                    <span>{shortedUrl}</span>
                    <button className={styles.copy} onClick={() => this.copyToClipboard(shortedUrl)}>Copy</button>
                </div>
                <div className={styles.offer}>
                    {this.state.isSubscribed ? this.renderSignupButton() : this.renderSubscribeButton()}
                </div>
            </div>
        )
    }

    renderSubscribeButton() {
        return(
            <div>
            <span>We will inform you about the statistics</span>
            <button className={styles.btn} onClick={this.subscribe}>Get notified</button>
            </div>
        )
    }

    renderSignupButton() {
        return(
            <div>
            <span>Want to manage, share, and track your links?</span>
            <button className={styles.btn} onClick={this.subscribe}>Sign UP</button>
            </div>
        )
    }
}

export default Information;